<template>
    <div id="tuesday">
      <el-card class="box-card">
        <el-form label-width="100px">
            <el-form-item label="我的府兵等级">
              <el-select @change="calcRate" v-model="soldier" filterable placeholder="请选择等级">
                <el-option label="无" :value="0"></el-option>
                <el-option v-for="(item, idx) in tables" :label="item.name" :value="item.value" :key="idx"></el-option>
              </el-select>
            </el-form-item>
            <el-form-item label="山贼等级">
              <el-select @change="calcRate" v-model="bandit" filterable placeholder="请选择等级">
                <el-option label="无" :value="0"></el-option>
                <el-option v-for="(item, idx) in bandits" :label="item.name" :value="item.value" :key="idx"></el-option>
              </el-select>
            </el-form-item>
            <el-form-item label="平叛功绩对决">
              <el-select @change="calcRate" v-model="banditAddtion" filterable placeholder="请选择等级">
                <el-option label="无" :value="0"></el-option>
                <el-option v-for="(item, idx) in addtions" :label="item.name" :value="item.value" :key="idx"></el-option>
              </el-select>
            </el-form-item>
          </el-form>
        <div slot="header" class="clearfix">
          <span>课业基本信息</span>
        </div>
        <el-table :data="tableData" style="width: 100%">
          <el-table-column prop="name" label="项目"></el-table-column>
          <el-table-column prop="score" label="基础分"></el-table-column>
          <el-table-column prop="totalScore" label="加倍分"></el-table-column>
        </el-table>
      </el-card>
      <el-card class="box-card">
        <div slot="header" class="clearfix">
          <span>需要数量</span>
        </div>
        <el-table v-if="newTableData" :data="newTableData" style="width: 100%">
          <el-table-column prop="name" label="项目"></el-table-column>
          <el-table-column prop="count_w" label="数量(万)"></el-table-column>
          <el-table-column prop="count" label="数量"></el-table-column>
        </el-table>
      </el-card>
    </div>
</template>
<script>
  export default {
    props: {
      form: Object,
    },
    data() {
      return {
        activeName: '1',
        tableData: [],
        tables: [
          { name: '1级府兵', value: 2 },
          { name: '2级府兵', value: 5 },
          { name: '3级府兵', value: 9 },
          { name: '4级府兵', value: 13 },
          { name: '5级府兵', value: 18 },
          { name: '6级府兵', value: 26 },
          { name: '7级府兵', value: 35 },
          { name: '8级府兵', value: 50 },
          { name: '9级府兵', value: 75 },
          { name: '10级府兵', value: 110 },
          { name: '11级府兵', value: 220 },
          { name: '12级府兵', value: 500 }
        ].sort(() => -1),
        bandits: [
          // { name: '1级山贼', value: 2 },
          // { name: '2级山贼', value: 5 },
          // { name: '3级山贼', value: 9 },
          // { name: '4级山贼', value: 13 },
          // { name: '5级山贼', value: 18 },
          // { name: '6级山贼', value: 26 },
          // { name: '7级山贼', value: 35 },
          // { name: '8级山贼', value: 50 },
          // { name: '9级山贼', value: 75 },
          // { name: '10级山贼', value: 110 },
          // { name: '11级山贼', value: 220 },
          // { name: '12级山贼', value: 500 },
          // { name: '14级山贼', value: 4300 },
          // { name: '15级山贼', value: 4400 },
          // { name: '16级山贼', value: 4500 },
          // { name: '17级山贼', value: 4600 },
          // { name: '18级山贼', value: 4700 },
          // { name: '19级山贼', value: 4800 },
          // { name: '20级山贼', value: 4900 },
          // { name: '21级山贼', value: 5000 },
          // { name: '22级山贼', value: 5100 },
          // { name: '23级山贼', value: 5200 },
          // { name: '24级山贼', value: 5300 },
          // { name: '25级山贼', value: 5400 },
          // { name: '26级山贼', value: 5000 },
          // { name: '27级山贼', value: 6000 },
          // { name: '28级山贼', value: 7000 },
          // { name: '29级山贼', value: 8000 },
          { name: '30级山贼', value: 9000 }
        ].sort(()=> -1),
        newTarget: 0,
        newTableData: [],
        addtion: 0,
        addtions: Array.from({length: 10}, (item, index) => {return {name: `${index + 1}级`, value: (index + 1) * 0.15}}),
        soldier: 0,
        bandit: 0,
        banditAddtion: 0
      };
    },
    watch: {
      form: {
        deep: true,
        handler(newVal, oldVal) {
          this.calcRate();
        }
      }
    },
    methods: {
      calcRate() {
        const totalLevel = (this.form.level1 + this.addtion + this.form.level2 + this.form.level3 + this.form.mul) + Math.max(this.form.other - 1, 0);
        const tableData = this.tables.filter((item, _) => {
          return item.value == this.soldier;
        }) || [];
        const banditData = this.bandits.filter((item, _) => {
          return item.value == this.bandit;
        }) || [];
        console.log('total level = ', totalLevel);
        for (let item of tableData) {
          item['score'] = item['value'];
          item['totalScore'] = parseFloat(totalLevel * item['value']).toFixed(0);
        }
        for (let item of banditData) {
          tableData.push({
            name: item['name'],
            score: item['value'],
            totalScore: parseFloat((totalLevel + this.banditAddtion) * item['value']).toFixed(0)
          });
        }
        this.tableData = tableData;
        let old = this.form.old;
        if (this.form.money > 0) {
          old += this.form.money * 3500;
        }
        this.newTarget = this.form.target - old;
        this.calcTable();
      },
      // 计算缺少功绩
      calcTable() {
        const newTableData = [];
        console.log('new target = ', this.newTarget);
        for(const item of this.tableData) {
          const count = Math.floor(this.newTarget / item['totalScore']);
          if (item.name.indexOf('山贼') !== -1) {
            newTableData.push({
              'name': item['name'], 'count': count + ' 次', 'count_w': 0
            });
          } else {
            newTableData.push({
              'name': item['name'], 'count': count, 'count_w': (count / 10000).toFixed(1)
            });
          }
        }
        this.newTableData = newTableData;
      }
    }
  };
</script>


