<template>
    <div id="tuesday">
      <el-card class="box-card">
        <div slot="header" class="clearfix">
          <span>招贤基本信息</span>
        </div>
        <el-table :data="tableData" style="width: 100%">
          <el-table-column prop="name" label="项目"></el-table-column>
          <el-table-column prop="score" label="基础分"></el-table-column>
          <el-table-column prop="totalScore" label="加倍分"></el-table-column>
        </el-table>
      </el-card>
      <el-card class="box-card">
        <div slot="header" class="clearfix">
          <span>需要招贤次数</span>
        </div>
        <el-table :data="newTableData" style="width: 100%">
          <el-table-column prop="name" label="项目"></el-table-column>
          <el-table-column prop="count" label="需要招贤次数"></el-table-column>
        </el-table>
      </el-card>
    </div>
</template>
<script>
  export default {
    props: {
      form: Object,
    },
    data() {
      return {
        activeName: '1',
        tableData: [
          {'name': '天下', 'score': 40000, 'totalScore': 40000},
          {'name': '势力/时运', 'score': 50000, 'totalScore': 50000}
        ],
        newTarget: 0,
        newTableData: [],
        addtion: 0,
        addtions: Array.from({length: 10}, (item, index) => {return {name: `${index + 1}级`, value: (index + 1) * 0.15}})
      };
    },
    watch: {
      form: {
        deep: true,
        handler(newVal, oldVal) {
          this.calcRate();
        }
      }
    },
    methods: {
      calcRate() {
        const totalLevel = (this.form.level1 + this.addtion + this.form.level2 + this.form.level3 + this.form.mul) + Math.max(this.form.other - 1, 0);
        let tableData = this.tableData;
        console.log('total level = ', totalLevel);
        for (let item of tableData) {
          item['totalScore'] = parseFloat(totalLevel * item['score']).toFixed(0);
        }
        this.newTarget = this.form.target - this.form.old;
        this.calcTable();
      },
      // 计算缺少功绩
      calcTable() {
        const newTableData = [];
        console.log('new target = ', this.newTarget);
        for(const item of this.tableData) {
          const count = Math.floor(this.newTarget / item['totalScore']);
          newTableData.push({
            'name': item['name'], 'count': count
          });
        }
        this.newTableData = newTableData;
      }
    }
  };
</script>


