<template>
    <div id="tuesday">
      <el-card class="box-card">
        <el-form label-width="100px">
            <el-form-item label="府兵等级">
              <el-select @change="calcRate" v-model="soldier" filterable placeholder="请选择等级">
                <el-option label="无" :value="0"></el-option>
                <el-option v-for="(item, idx) in tables" :label="item.name" :value="item.value" :key="idx"></el-option>
              </el-select>
            </el-form-item>
            <el-form-item label="争锋功绩对决">
              <el-select @change="calcRate" v-model="addtion" filterable placeholder="请选择等级">
                <el-option label="无" :value="0"></el-option>
                <el-option v-for="(item, idx) in addtions" :label="item.name" :value="item.value" :key="idx"></el-option>
              </el-select>
            </el-form-item>
            <el-form-item label="是对决州">
              <el-switch
                v-model="isEnemy"
                @change="calcRate"
                active-color="#13ce66"
                inactive-color="#ff4949">
              </el-switch>
            </el-form-item>
          </el-form>
        <div slot="header" class="clearfix">
          <span>课业基本信息</span>
        </div>
        <el-table :data="tableData" style="width: 100%">
          <el-table-column prop="name" label="项目"></el-table-column>
          <el-table-column prop="score" label="基础分"></el-table-column>
          <el-table-column prop="totalScore" label="加倍分"></el-table-column>
        </el-table>
      </el-card>
      <el-card class="box-card">
        <div slot="header" class="clearfix">
          <span>需要数量</span>
        </div>
        <el-table v-if="newTableData" :data="newTableData" style="width: 100%">
          <el-table-column prop="name" label="项目"></el-table-column>
          <el-table-column prop="count_w" label="数量(万)"></el-table-column>
          <el-table-column prop="count" label="数量"></el-table-column>
        </el-table>
      </el-card>
    </div>
</template>
<script>
  export default {
    props: {
      form: Object,
    },
    data() {
      return {
        activeName: '1',
        tableData: [],
        tables: [
          { name: '4级府兵', value: '4级府兵' },
          { name: '5级府兵', value: '5级府兵' },
          { name: '6级府兵', value: '6级府兵' },
          { name: '7级府兵', value: '7级府兵' },
          { name: '8级府兵', value: '8级府兵' },
          { name: '9级府兵', value: '9级府兵' },
          { name: '10级府兵', value: '10级府兵' },
          { name: '11级府兵', value: '11级府兵' },
          { name: '12级府兵', value: '12级府兵' }
        ].sort(() => -1),
        minors: [
          { name: '4级府兵', value: 1 },
          { name: '5级府兵', value: 2 },
          { name: '6级府兵', value: 3 },
          { name: '7级府兵', value: 4 },
          { name: '8级府兵', value: 5 },
          { name: '9级府兵', value: 6 },
          { name: '10级府兵', value: 7 },
          { name: '11级府兵', value: 8 },
          { name: '12级府兵', value: 9 },
        ],
        seriously: [
          { name: '4级府兵', value: 1 },
          { name: '5级府兵', value: 2 },
          { name: '6级府兵', value: 3 },
          { name: '7级府兵', value: 4 },
          { name: '8级府兵', value: 5 },
          { name: '9级府兵', value: 6 },
          { name: '10级府兵', value: 8 },
          { name: '11级府兵', value: 10 },
          { name: '12级府兵', value: 18 },
        ],
        died: [
          { name: '4级府兵', value: 4 },
          { name: '5级府兵', value: 5 },
          { name: '6级府兵', value: 6 },
          { name: '7级府兵', value: 7 },
          { name: '8级府兵', value: 8 },
          { name: '9级府兵', value: 10 },
          { name: '10级府兵', value: 12 },
          { name: '11级府兵', value: 15 },
          { name: '12级府兵', value: 36 },
        ],
        newTarget: 0,
        newTableData: [],
        addtion: 0,
        addtions: Array.from({length: 10}, (item, index) => {return {name: `${index + 1}级`, value: (index + 1) * 0.15}}),
        soldier: 0,
        isEnemy: 0,
      };
    },
    watch: {
      form: {
        deep: true,
        handler(newVal, oldVal) {
          this.calcRate();
        }
      }
    },
    methods: {
      calcRate() {
        let totalLevel = (this.form.level1 + this.addtion + this.form.level2 + this.form.level3 + this.form.mul) + Math.max(this.form.other - 1, 0);
        if (this.isEnemy) {
          totalLevel += 3;
        }
        const tableData = [];
        const minorsItem = this.minors.filter((item, _) => item.name == this.soldier);
        const seriouslyItem = this.seriously.filter((item, _) => item.name == this.soldier);
        const diedItem = this.died.filter((item, _) => item.name == this.soldier);
        console.log('total level = ', totalLevel);
        for (const item of minorsItem) {
          tableData.push({
            'name': '轻伤' + item.name, score: item.value, totalScore: totalLevel * item.value
          })
        }
        for (const item of seriouslyItem) {
          tableData.push({
            'name': '重伤' + item.name, score: item.value, totalScore: totalLevel * item.value
          })
        }
        for (const item of diedItem) {
          tableData.push({
            'name': '阵亡' + item.name, score: item.value, totalScore: totalLevel * item.value
          })
        }
        this.tableData = tableData;
        let old = this.form.old;
        if (this.form.money > 0) {
          old += this.form.money * 3500;
        }
        this.newTarget = this.form.target - old;
        this.calcTable();
      },
      // 计算缺少功绩
      calcTable() {
        const newTableData = [];
        console.log('new target = ', this.newTarget);
        for(const item of this.tableData) {
          const count = Math.floor(this.newTarget / item['totalScore']);
          newTableData.push({
            'name': item['name'], 'count': count, 'count_w': (count / 10000).toFixed(1)
          });
        }
        this.newTableData = newTableData;
      }
    }
  };
</script>


